@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");

body {
    margin: 0;
    font-family: "Lato", sans-serif;
    color: #262626;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

.page-content {
    min-height: calc(100vh - 60px);
    background: #fafafa;
}

/*
    NAVBAR
-------------*/
nav {
    padding: 10px 5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: white;
    border-bottom: 2px solid #ececec;
}
@media (max-width: 992px) {
    nav {
        padding: 10px 3rem;
    }
}
@media (max-width: 768px) {
    nav {
        padding: 10px 2rem;
    }
}
@media (max-width: 576px) {
    nav {
        padding: 0;
        flex-direction: column;
    }
}

h1 {
    color: #262626;
}

.nav-links {
    display: flex;
}
.nav-links li {
    margin: 0 1rem;
}
@media (max-width: 576px) {
    .nav-links {
        flex-direction: column;
    }
    .nav-links li {
        margin: 1rem;
        text-align: center;
    }
}
.nav-links a {
    padding: 10px 0;
    color: #262626;
    font-size: 1.1rem;
    position: relative;
}
.nav-links a::after {
    content: "";
    height: 2px;
    width: 100%;
    background: #0095f6;
    border-radius: 25px;
    position: absolute;
    left: 0;
    bottom: 0;
    transition: 0.3s;
    transform: scaleX(0);
}
.nav-links a:hover::after {
    transform: scaleX(100%);
}

.toggle-btn {
    display: none;
}

@media (max-width: 576px) {
    .mobile-header {
        border-bottom: 2px solid #efefff;
        width: 100%;
        text-align: center;
    }
    .toggle-btn {
        display: block;
        position: absolute;
        right: 1rem;
        top: 1rem;
        font-size: 1.5rem;
        border: none;
        background: none;
        color: #262626;
    }
    .menu {
        transition: all 0.3s ease;
        width: 100%;
    }
    .menu-hide {
        position: absolute;
        top: 71px;
        transform: translateX(-100%);
    }
}

/*
    FOOTER
------------*/
footer {
    background: #fafafa;
    text-align: center;
    color: #b3b1b1;
    padding: 1rem;
}
footer p {
    margin: 0;
}
footer a {
    color: #b3b1b1;
    transition: color 0.3s ease;
}
footer a:hover {
    color: #262626;
}
